var isChinaMobile = /^134[0-8]\\d{7}$|^(?:13[5-9]|147|15[0-27-9]|178|18[2-478])\\d{8}$/; //
var isChinaUnion = /^(?:13[0-2]|145|15[56]|176|18[56])\\d{8}$/; //
var isChinaTelcom = /^(?:133|153|177|18[019])\\d{8}$/; //1349 
var isOtherTelphone = /^170([059])\\d{7}$/; //      
 export default {
	    checkMobile(telphone) {
		     telphone =  telphone ;
		 
		  
		   
		  
		if (telphone.length !== 11) {
			return this.setReturnJson(false, '');
		} else {
			 
			if (isChinaMobile.test(telphone)) { 
				console.log("msgmsgmsgmsg","")
				return ''
				 
				//  this.setReturnJson(true, '', {
				// 	name: 'ChinaMobile'
				// });
			} else if (isChinaUnion.test(telphone)) {
				return this.setReturnJson(true, '', {
					name: 'ChinaUnion'
				});
			} else if (isChinaTelcom.test(telphone)) {
				return this.setReturnJson(true, '', {
					name: 'ChinaTelcom'
				});
			} else if (isOtherTelphone.test(telphone)) {
				var num = isOtherTelphone.exec(telphone);
				return this.setReturnJson(true, '', {
					name: ''
				});
			} else {
				return this.setReturnJson(false, '');
			}
			
			 
		}
	},
	/**
	 *  ()
	 * @param fn
	 * @param delay
	 * @returns {Function}
	 * @constructor
	 */
	Debounce(fn, t)  {
	    let delay = t || 500;
	    let timer;
	    console.log(fn)
	    console.log(typeof fn)
	    return function () {
	        let args = arguments;
	        if(timer){
	            clearTimeout(timer);
	        }
	        timer = setTimeout(() => {
	            timer = null;
	            fn.apply(this, args);
	        }, delay);
	    }
	},
	 
	 
	/**
	 * 
	 * @param fn
	 * @param interval
	 * @returns {Function}
	 * @constructor
	 */
	 Throttle(fn, t)  {
	    let last;
	    let timer;
	    let interval = t || 500;
	    return function () {
	        let args = arguments;
	        let now = +new Date();
	        if (last && now - last < interval) {
	            clearTimeout(timer);
	            timer = setTimeout(() => {
	                last = now;
	                fn.apply(this, args);
	            }, interval);
	        } else {
	            last = now;
	            fn.apply(this, args);
	        }
	    }
	},
	    setReturnJson(status, msg, data) {
		if (typeof status !== 'boolean' && typeof status !== 'number') {
			status = false;
		}
		if (typeof msg !== 'string') {
			msg = '';
		}
		 
		return {
			'status': status,
			'msg': msg,
			'data': data
		};
	}
	}
   
